.nav-fixed {
    background-color: white;
    position: fixed;
    width: 100%;
    box-shadow: 0 .3rem .4rem #444;
    opacity: 0.97;
    
    a{
        
        img{
            width: 50% !important;
        }
    }

    .navigation {
        a {
            color:black !important;
            &:hover{
                color: $secondaryColor !important;
            }
        }

    }

}

.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    //padding-top: 1rem;

    img{
        width: 50%;
    }

    .navigation{
        right:0;
        position: fixed;
        background: #464655;
        clip-path: circle(0px at top right);
        height: 100%;
        transition: clip-path ease-in-out 700ms;

        ul{
            margin: 0;
            padding: 0;
            list-style: none;
        }
        li{
            border-bottom: 1px solid #575766;
            &:last-child {
                border-bottom: none;
            }
        }
        
        a{
            color: $white;
            font-size: 1.6rem;
            /* Block permite que se renderice como bloque es decir como si fuera un div y de esta forma respete las relaciones de tamaño y espaciado entre otros elementos html */
            display: block;
            padding: 2em 6em;
            text-decoration: none;
            text-transform: uppercase;
            
            &:hover, &:focus{
                background: #DEAD87;
                color: #464655;
            }
                        
        }
    }
    .site-nav-open{
        clip-path: circle(150% at top right);
    }
    
    .site-nav--icon{
        display: inline-block;
        font-size: 1.5em;
        margin-right: 1em;
        width: 1.1em;
        text-align: right;
        color: rgba(255, 255, 255, .4);
    }
    
    .menu-toggle{
       
        padding: 1em;
        position: absolute;
        top: 2rem;
        right: 2rem;
        
    }

    .menu-open{

        .hamburger{
            transform: rotate(45deg);
        }

        .hamburger::before{
            opacity: 0;
        }

        .hamburger::after{
            transform: translateY(-3px) rotate(-90deg);
        }
    }

    .hamburger,
    .hamburger::before,
    .hamburger::after{
        content: '';
        display: block;
        background: #ebebd3;
        height: 3px;
        width: 2.6rem;
        border-radius: 3px;
        transition: all ease-in-out 500ms;
    }
    .hamburger::before{
        transform: translateY(-7px);
    }
    .hamburger::after{
        transform: translateY(4px);
    }

    /* De tablet hacia arriba */
    @include tablet {
        

        a{
            img{
                width:100%;
            }
        }

        .menu-toggle{
            display: none;
        } 
        
        .navigation{
            
            display: flex;
            align-items: center;
            height: auto;
            position: relative;
            background: transparent;
            float: right;
            margin-right: 10px;
            clip-path: initial;
    
            li{
                display: inline-block;
                border: none;
            }
    
            a{
                padding: 0;
                margin-left: 3rem;
                //text-transform: lowercase;
                //text-transform: capitalize;
    
                &:hover, &:focus{
                    background: transparent;
                    color: white;
                }
    
            }
        } 
        
        .site-nav--icon{
            display: none;
    
            &:hover{
                background: transparent;
            }
        }
        
    }
}

