/** Media Queries **/
@mixin phone {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin button($bgColor, $hoverColor ,$display, $border) {
    
    background-color: $bgColor;
    border: $border;
    border-radius: 8px;
    
    color: white;
    cursor: pointer;
    display: $display;
    font-size: 1.6rem;
    font-weight: 500;
    
    margin-top: 2rem;
    padding: 1.6rem 2rem;
    text-transform: uppercase;
    text-decoration: none;    
    text-align: center;    
    transition: background-color .4s;
    

    &:hover {
        background-color: darken($hoverColor, 5);
        box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
        cursor: pointer;
    }
}