

.button-primary{
    @include button($primaryColor, $primaryColor, block, none);
}


.button-secondary{
    @include button($secondaryColor, $secondaryColor, block, none);

}

.button-third{
    @include button($thirdColor, $thirdColor, block, none);

}

.button-transparent{    
    @include button(transparent, $secondaryColor, block,  2px solid $secondaryColor)
    
}