.form-items{
    h3{
        margin: 4rem;
    }

    .form-itemsContact{

        @include grid(2, 0);
        
    
        h3, p{
            margin: 1rem;
            text-align: center;
        }
        .item-contact{
            align-items: center;
            display: flex;
            flex-direction: column;
            max-width: 30rem;
            line-height: 1.2;
        }

        @include tablet {
                @include grid(4, 0);           
        }
    }
}
.form-header{
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    line-height: 1.2;
    h1, h2, p {
        text-align: center;
        max-width: 50rem;
    }

    h1 {
        font-weight: bold;
    }
}

.form-Maps{
    margin: 4rem 0;

    @include tablet {
        @include grid(1, 0);
        .maps{
            text-align: center;
            align-items: center;
            
            /* iframe{
                width: 1000 ;
                height: 130;
                 
            } */
        }           
    }

    @include desktop {
        @include grid(2, 0);           
    }

    .form-fill{
        margin-right: 0;
        p {
            font-size: 1.4rem;
            color: $darkGrey;
            margin: 2rem 0 0 0;
        }

        @include tablet {
            margin-right: 2rem;
        }
    
        legend {
            font-size: 2rem;
            color: $darkGrey;
        }
    
        label {
            font-weight: $bold;
            text-transform: uppercase;
            display: block;
        }
        input:not([type="submit"]), 
        textarea,
        select {
            padding: 1rem;
            display: block;
            width: 100%;
            background-color: $white;
            border: 1px solid $grey;
            border-radius: 1rem;
            margin-bottom: 2rem;
        }
        
        textarea {
            height: 20rem;
        }
    
        .forma-contacto {
            max-width: 30rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        input[type="radio"] {
            margin: 0;
            width: auto;
        }

        .text-danger{
            color: red;
            margin: 0;
        }

        .btn-form {
            width: 100%;
        }
    }

    .maps{
        align-items: center;
        margin: 2rem ;
        
        iframe{
            @include phone{
                width: fit-content ; 
                //height: 20rem;
            }
            
            @include tablet{
                width: 80rem ; 
                height: 42rem;
            }

            @include desktop{
                width: 60rem ; 
                height: 62rem;
            }
        }
        
    }
}