.header-p {
    background-color: darken($color: white, $amount: 5);
    box-shadow: 0 .3rem .4rem #444;
    //padding: 0 0 3rem 0;

    /* @include tablet {
        min-height: 50vh;
    } */
}

.navbar-p{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
    //padding-top: 1rem;

    img{
        width: 100%;
    }

    .navigation-p{
        display: flex;
        align-items: center;
        height: auto;
        position: relative;
        background: transparent;
        float: right;
        margin-right: 10px;
        clip-path: initial;

        li{
            display: inline-block;
            border: none;
        }

        a{
            font-size: 2rem;
            padding: 0;
            margin-left: 3rem;

            &:hover{
                color: lighten($color: black, $amount: 50);
            }

        }

        i{
            margin-right: 1rem;
        }
    }
}

.privacy-content{
    align-items:center;
    display: flex;
    flex-direction:column;
    min-height: 100vh;

    h1, p {
        text-align: center;
        margin: 3rem 0;
    }

    h1{
        font-weight: bold;
        margin: 3rem 0;
    }

    h2 {
        font-weight: $delgada;
    }

    p {
        margin:0;
    }

    span {
        display: block;
        color: lighten($color: #000000, $amount: 50);
        margin: 2rem 2rem;
    }

    @include tablet{
        p{
            text-align: left;
        }
    }

}
.footer-p {
    position: absolute;
}

