.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* De tablet hacia arriba */ }
  .navbar img {
    width: 50%; }
  .navbar .navigation {
    right: 0;
    position: fixed;
    background: #464655;
    clip-path: circle(0px at top right);
    height: 100%;
    transition: clip-path ease-in-out 700ms; }
    .navbar .navigation ul {
      margin: 0;
      padding: 0;
      list-style: none; }
    .navbar .navigation li {
      border-bottom: 1px solid #575766; }
      .navbar .navigation li:last-child {
        border-bottom: none; }
    .navbar .navigation a {
      color: #FFFFFF;
      font-size: 1.6rem;
      /* Block permite que se renderice como bloque es decir como si fuera un div y de esta forma respete las relaciones de tamaño y espaciado entre otros elementos html */
      display: block;
      padding: 2em 6em;
      text-decoration: none;
      text-transform: uppercase; }
      .navbar .navigation a:hover, .navbar .navigation a:focus {
        background: #DEAD87;
        color: #464655; }
  .navbar .site-nav-open {
    clip-path: circle(150% at top right); }
  .navbar .site-nav--icon {
    display: inline-block;
    font-size: 1.5em;
    margin-right: 1em;
    width: 1.1em;
    text-align: right;
    color: rgba(255, 255, 255, 0.4); }
  .navbar .menu-toggle {
    padding: 1em;
    position: absolute;
    top: 2rem;
    right: 2rem; }
  .navbar .menu-open .hamburger {
    transform: rotate(45deg); }
  .navbar .menu-open .hamburger::before {
    opacity: 0; }
  .navbar .menu-open .hamburger::after {
    transform: translateY(-3px) rotate(-90deg); }
  .navbar .hamburger,
  .navbar .hamburger::before,
  .navbar .hamburger::after {
    content: '';
    display: block;
    background: #ebebd3;
    height: 3px;
    width: 2.6rem;
    border-radius: 3px;
    transition: all ease-in-out 500ms; }
  .navbar .hamburger::before {
    transform: translateY(-7px); }
  .navbar .hamburger::after {
    transform: translateY(4px); }
  @media (min-width: 768px) {
    .navbar a img {
      width: 100%; }
    .navbar .menu-toggle {
      display: none; }
    .navbar .navigation {
      display: flex;
      align-items: center;
      height: auto;
      position: relative;
      background: transparent;
      float: right;
      margin-right: 10px;
      clip-path: initial; }
      .navbar .navigation li {
        display: inline-block;
        border: none; }
      .navbar .navigation a {
        padding: 0;
        margin-left: 3rem; }
        .navbar .navigation a:hover, .navbar .navigation a:focus {
          background: transparent;
          color: white; }
    .navbar .site-nav--icon {
      display: none; }
      .navbar .site-nav--icon:hover {
        background: transparent; } }

