@include tablet {
    .aboutUs{
        min-height: 60rem;
        @include grid(2, 0);
        
    }
}

.aboutUs-background {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 2rem;
    /* background-position: center center;
    background-size: cover; */
    @include tablet{
        img{
            border-radius: 2rem 0 0 2rem;
        }
    }
}

.aboutUs-description {
    //align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 5rem ;
    padding: 2rem;
    
    h2 {
        //font-size: 3rem;
        //color: $white;
        font-weight: $bold;
        margin-bottom: 2rem;
        max-width: 60rem;
        line-height: 1.3;
        text-align: center;
        //text-transform: uppercase;
        
    }

    
    .aboutUs-p {
        color: darken($color: white, $amount: 50);
        font-size: 1.7rem;
        font-weight: $regular;
        margin-top: 0;
        margin-bottom: 2rem;
        text-align: center;
    }

    


    @include tablet{
        //background-color: #f5f5f5;
        margin: 5rem;

        .aboutUs-p {
            color: lighten($color: $black, $amount: 50);
            text-align: left;
        }

        .aboutUs-objective{
            margin-bottom: 20px;
            border-left-width: 4px;
            padding-left: 20px;
            border-left-color: $secondaryColor ;
            border-left-style: solid ;
        }

    
    }
}

