/* .webp es una clase que viene por defecto en html para permitir o no ese formato */
.no-webp .header{
    background-image: url(../img/portadaSpeed4.jpg);
}
.webp .header {
    background-image: url(../img/portadaSpeed5.webp);
    //background-image: url(../img/Products_bgb.webp);
}


.header {
    background-color: $darkGrey;
    //padding: 0 0 3rem 0;
    
    
    background-position: center center;
    background-size:cover;

    @include tablet {
        min-height: 100vh;
    }

    .overlay{
        padding: 0 0 3rem 0;
        min-height: 100vh;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#000000+0,000000+100&0+13,0.3+30,0.6+52,0.7+73,0.8+100 */
        background: -moz-linear-gradient(-45deg,  rgba(0,0,0,0) 0%, rgba(0,0,0,0) 13%, rgba(0,0,0,0.3) 30%, rgba(0,0,0,0.6) 52%, rgba(0,0,0,0.7) 73%, rgba(0,0,0,0.8) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,  rgba(0,0,0,0) 0%,rgba(0,0,0,0) 13%,rgba(0,0,0,0.3) 30%,rgba(0,0,0,0.6) 52%,rgba(0,0,0,0.7) 73%,rgba(0,0,0,0.8) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg,  rgba(0,0,0,0) 0%,rgba(0,0,0,0) 13%,rgba(0,0,0,0.3) 30%,rgba(0,0,0,0.6) 52%,rgba(0,0,0,0.7) 73%,rgba(0,0,0,0.8) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#cc000000',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

    }
    .contenido-header {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center; /* Centra en columna */
        
       
        .image-logo{
            align-items: center;
            display:flex;
        
            justify-content: center;
            //margin-bottom: 2rem;
            margin-left: 1rem;
            
            text-align: center;
        
            &:first-of-type {
                margin-left: 0;
            }
        
            picture{
                width: 15rem;
            }
        }
    
        h1 , h3, h4, p{
            text-align: center; /* No se requiere porque h1 ya está definido para estar alineado al centro */
            color: $white;
            //color: black;
            //font-weight: $bold;
            line-height: 1.2; /* Esto permite que el texto haga salto de línea si no cabe en la pantalla */
            max-width: 50rem;
            margin-bottom: 1rem;   
            
        }


        h1 {
            color: $white;
            font-weight: $bold;
            //color: black;
        }
        .buttons-header{
            display: flex;
            flex-direction: column;     
           
            a{
                margin-right: 0;
                margin-top: 2rem;
                &:last-of-type {
                    margin-right: 0;
            }
        }


            @include tablet {
                display: flex;
                flex-direction: row;
                align-items: center;
                padding: 2rem;


                a{
                    margin-right: 2rem;
                    margin-top: 0;
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
            }
        }
       
        h4{
            font-size: 2rem;
            color: darken($color: white, $amount: 15);
            margin-top: 0;
        }
        
        .title{
            font-size: 4.5rem;
        }
    
        @include tablet {
            align-items: left;
            min-height: 50rem;
            .image-logo{
                picture{
                    display: none;
                }
            }

            h4{
                margin-top: 2rem;
            }
            
        }
    
    }


    
}


