@include tablet {
    .clients {
        @include grid(3, 0);
    }

}

@include desktop {
    .clients {
        @include grid(4, 0);
    }
}

.clients-group {
    padding: 2rem;
}

.clients-header{
    margin-bottom: 2rem;
    padding: 2rem;
    h1 {
        color: white;
        font-weight: bold;
    }
}
.image{
    align-items: center;
    display: flex;

    justify-content: center;
    margin-bottom: 2rem;
    margin-left: 1rem;
    
    text-align: center;

    &:first-of-type {
        margin-left: 0;
    }

    picture{
        width: 20rem;
    }
}