.no-webp .service-product{
    background-image: url(../img/Products.jpg);
}
.webp .service-product {
    background-image: url(../img/Products.webp);
}
.service-product{
    background-position: center center;
    background-size: cover;
    opacity: 1;
       
}

@include tablet {
    .services {
        @include grid(2, 0);
        margin-bottom: 2rem;
    }
}


.service-header {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    h2, p {
        text-align: center;
        max-width: 70rem;
    }

    h2 {
        margin-bottom: 1rem;
        font-weight: bold;

    }
}
.service {
    background-color: lighten($secondaryColor, 15);
    border: 1px solid darken($grey, 10);
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 1rem;
    margin-bottom: 2rem;
    margin-right: 0;

    /* &:last-of-type {
        margin-right: 0;
    } */

    picture {
        img {
            border-radius: 1rem 1rem 0 0;

        }
        
    }
    @include tablet{
        margin: 0 2rem 2rem;
        //margin-right: 2rem;
    }
    .service-detail {

        padding: 2rem;

    }

    h3, p {
        text-align: left;
        margin-bottom: 2rem;
        //margin: 0;
    }

    h3 {
        line-height: 1.2;
        font-weight: 500;
    }
 
}

.service-end{
    
  
    .service-product{
        @include tablet{
            margin: 2rem;
        }
        .overlay {
            padding: 0 0 3rem 0;
            width: 100%;
            min-height: 55vh;
            border-radius: 1rem;
    
            /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#2f284f+0,2f284f+100&0.6+1,0.6+63,0.6+100 */
            background: -moz-linear-gradient(top,  rgba(47,40,79,0.6) 0%, rgba(47,40,79,0.6) 1%, rgba(47,40,79,0.6) 63%, rgba(47,40,79,0.6) 100%); /* FF3.6-15 */
            background: -webkit-linear-gradient(top,  rgba(47,40,79,0.6) 0%,rgba(47,40,79,0.6) 1%,rgba(47,40,79,0.6) 63%,rgba(47,40,79,0.6) 100%); /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to bottom,  rgba(47,40,79,0.6) 0%,rgba(47,40,79,0.6) 1%,rgba(47,40,79,0.6) 63%,rgba(47,40,79,0.6) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#992f284f', endColorstr='#992f284f',GradientType=0 ); /* IE6-9 */
        }

        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;

        text-align: center;
        border-radius: 1rem;
        border: 1px solid darken($grey, 10);
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);

        
        
        .service{
            margin-bottom:0;
            width: 100%;

            picture{
                img{
                    width:100%;
                }
            }
            
        }
        
        
        .service-detail{
                         
            width: 100%;
            
            p{
                
                //border-bottom: 1px solid darken($grey, 10);
                color: $white;
                font-weight: $bold;
                line-height: 1.2; /* Esto permite que el texto haga salto de línea si no cabe en la pantalla */
                
                margin-bottom: 1rem;
            }
    
            h3{
                color: $white;
                font-weight: 500;
            }

        
            table {
                align-items: center;
                text-align: center;
                border-collapse: collapse;
                width: 100%;
                

            }
              
            th {
            padding: 8px;
            text-align: center;
                
            border-bottom: 1.5px solid #ddd;
            }
            td {
                padding:2rem;
                margin-top:4rem;
                  
            }
              
            /* p:hover {
                background-color:$thirdColor;
                color: black;
                padding:2rem;
                transition: padding 1s;
            } */
        }
    }  

    
}

